//Home button

import { NavLink } from 'react-router-dom'
import React from 'react'
import styled from 'styled-components'

import HomeBtn from './HomeBtn.js';

const Power = styled.button`
position: fixed;
top: 2.5rem;
left: 50%;
transform: translate(-50%, 0);

background-color: #eb8334;
padding: .3em;
opacity: 75%;
border-radius: 50%;
border: 1.5px solid #000;
width: 3rem;
height: 3rem;

display: flex;
justify-content: center;
align-items: center;
z-index: 3;

cursor: pointer;

&:hover {
    background-color: rgba(255, 117, 13);
    box-shadow: 0 0 4px 8px rgba(255, 117, 13, 0.3);
}
`
const HomeButton = () => {
    return (
        <Power>
             <HomeBtn />
            <NavLink to="/">
         
        </NavLink>
        </Power>
    )
}

export default HomeButton;